<template>
    <div class="differenceStrategyLibrary">
        <div class="header">
            <div class="selectNav">
                <el-select size="small" class="input_td" v-model="selectParams.dsjVal" clearable placeholder="请选择地市局" @change="dsjChange">
                    <el-option
                        v-for="item in selectParams.dsjOptions"
                        :key="item.ORG_CODE"
                        :label="item.ORG_NAME"
                        :value="item.ORG_CODE"
                    ></el-option>
                </el-select>
                <el-select v-model="selectParams.xqjVal" clearable placeholder="请选择县区局" class="input_td" size="small" @change="qxjChange">
                    <el-option
                        v-for="item in selectParams.xqjOptions"
                        :key="item.ORG_CODE"
                        :label="item.ORG_NAME"
                        :value="item.ORG_CODE"
                    >
                    </el-option>
                </el-select>
                <el-select size="small" class="input_td" v-model="selectParams.gdsVal" clearable placeholder="请选择供电所">
                    <el-option
                        v-for="item in selectParams.gdsOptions"
                        :key="item.ORG_CODE"
                        :label="item.ORG_NAME"
                        :value="item.ORG_CODE"
                    ></el-option>
                </el-select>
                <el-input
                    class="input_300"
                    placeholder="请输入停电工单号"
                    size="small"
                    v-model="tdgdhInput"
                    clearable>
                </el-input>
                <el-input
                    class="input_300"
                    placeholder="请输入用户编号"
                    size="small"
                    v-model="yhCodeInput"
                    clearable>
                </el-input>
                <el-button type="primary" size="small" icon="el-icon-search" @click="getQueryDataList">查询</el-button>
                <el-button type="primary" size="small" @click="notifyChange">通知模板维护</el-button>
                <el-button type="primary" size="small" @click="addNew">新增通知模板</el-button>
            </div>
        </div>
        <div class="content">
            <div class="title">第2次停电通知内容维护</div>
            <div class="tableTd">
                <el-table
                    v-loading="loading"
                    border
                    :data="tableData"
                    style="width: 100%"
                    :row-class-name="tableRowClassName">
                    <el-table-column
                        label="序号"
                        align="center"
                        type="index"
                        width="70">
                    </el-table-column>
                    <el-table-column
                        prop="powerFailureNum"
                        label="停电事件工单号">
                    </el-table-column>
                    <el-table-column
                        prop="powerFailureDesc"
                        label="停电事件描述">
                    </el-table-column>
                    <el-table-column
                        prop="outageType"
                        label="停电类型">
                    </el-table-column>
                    <el-table-column
                        prop="timeDelay"
                        label="计划开始时间是否延迟">
                    </el-table-column>
                    <el-table-column
                        prop="firstSendMsg"
                        label="第一次短信发送时间">
                    </el-table-column>
                    <el-table-column
                        prop="firstSendFailureNum"
                        label="第一次短信发送失败客户数">
                    </el-table-column>
                    <el-table-column
                        prop="tdsjkssj"
                        label="停电事件开始时间">
                    </el-table-column>
                    <el-table-column
                        prop="secondSendMsgNum"
                        label="需第二次短信发送客户数">
                    </el-table-column>
                    <el-table-column
                        prop="userNo"
                        label="用户编号">
                    </el-table-column>
                    <el-table-column
                        prop="userName"
                        label="用户名称">
                    </el-table-column>
                    <el-table-column
                        prop="userAdress"
                        label="用电地址">
                    </el-table-column>
                    <el-table-column
                        prop="cityBureau"
                        label="地市局">
                    </el-table-column>
                    <el-table-column
                        prop="countyBureau"
                        label="县区局">
                    </el-table-column>
                    <el-table-column
                        prop="powerSupply"
                        label="供电所">
                    </el-table-column>
                    <el-table-column
                        prop="msgNoticeStatus"
                        label="短信通知信息配置状态">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        width="120"
                        label="短信通知配置">
                        <template slot-scope="scope">
                            <el-link type="danger" @click="operateList(scope.row)">编辑{{scope.name}}</el-link>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="operatorName"
                        label="操作人">
                    </el-table-column>
                    <el-table-column
                        prop="operatorTime"
                        label="操作时间">
                    </el-table-column>
                </el-table>
                <div class="block">
                    <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="pages.pageNo"
                        :page-sizes="[8, 10, 20, 50]"
                        :page-size="pages.pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="pages.total">
                    </el-pagination>
                </div>
            </div>
        </div>

        <el-dialog
            class="notifyConfig-dialog"
            :visible.sync="notifyConfigVisible"
            width="80%"
            :before-close="notifyConfigHandleClose"
            :close-on-click-modal="false"
            :close-on-press-escape="false"
        >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ '通知内容配置' }}</span>
      </span>
            <div class="dialog-content">
                <el-form class="notifyConfig-form" v-model="notifyConfigForm" label-width="160px">
                    <el-row>
                        <el-col :span="12" style="min-height: 340px;" v-loading="tempLoading">
                            <el-form-item label="通知内容配置选择：" prop="tzmbnr">
                                <el-input style="width: calc(100% - 70px);margin-right: 10px"  placeholder="请输入模板名称" v-model="tempSelectName" size="small" clearable></el-input>
                                <el-button size="small" type="primary" @click="getTempList">查询</el-button>
                                <span class="tag"
                                      v-for="item in tempList"
                                      :key="item.id"
                                      @click="changeTemp(item)"
                                      :class="{isSelect:item.id===smsId}"
                                >{{item.templateName}}
                                </span>
                                <div class="block">
                                    <el-pagination
                                        @size-change="handleSizeChange2"
                                        @current-change="handleCurrentChange2"
                                        :current-page="pages2.pageNo"
                                        :page-sizes="[8, 10, 20, 50]"
                                        :page-size="pages2.pageSize"
                                        layout="total, sizes, prev, pager, next, jumper"
                                        :total="pages2.total">
                                    </el-pagination>
                                </div>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="通知内容选择结果：" prop="tzmbnr">
                                <span class="text">{{notifyConfigForm.tznrpzxzjg}}</span>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="通知时间配置选择：" prop="time">
                                <el-date-picker
                                    size="small"
                                    class="input"
                                    v-model="notifyConfigForm.time"
                                    type="datetime"
                                    value-format="yyyy-MM-dd hh:mm:ss"
                                    placeholder="请选择时间"
                                ></el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" type="primary" @click="smsConfigSet">确认</el-button>
                <el-button size="small" @click="notifyConfigHandleClose">关闭</el-button>
            </div>
        </el-dialog>
<!--        通知短信模板维护-->
        <el-dialog
            class="notifyChange-dialog"
            :visible.sync="notifyChangeVisible"
            width="80%"
            top="6vh"
            :before-close="notifyChangeHandleClose"
            :close-on-click-modal="false"
            :close-on-press-escape="false"
        >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ '通知模板配置' }}</span>
      </span>
            <div class="dialog-content">
                <div class="dialog-content-title">
                    <el-input style="width: 260px;margin: 0px 10px 6px 0px" placeholder="请输入模板名称" v-model="mbmc" size="small" clearable></el-input>
                    <el-button size="small" type="primary" @click="getNotifyChangeList">查询</el-button>
                </div>
                <div class="dialog-content-main" style="height: 500px">
                    <bdaTable
                        :tableData="notifyChangeData"
                        :isPage="true"
                        :pageConfig="notifyChangePageConfig"
                        @changePage="changePage"
                        @operate="operateNotifyChangeList"
                    ></bdaTable>
                </div>
            </div>
        </el-dialog>

<!--        新增短信通知模板-->
        <el-dialog
            class="addNew-dialog"
            :visible.sync="addNewVisible"
            width="80%"
            top="6vh"
            :before-close="addNewHandleClose"
            :close-on-click-modal="false"
            :close-on-press-escape="false"
        >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ addNewTitle }}</span>
      </span>
            <div class="dialog-content">
                <el-form class="addNew-form" v-model="addNewForm" label-width="120px">
                    <el-row>
                        <el-col :span="24">
                            <el-form-item label="通知模板名称：" prop="tzmbnr">
                                <el-input
                                    placeholder="请输入通知模板名称"
                                    size="mini"
                                    class="input"
                                    v-model="addNewForm.tzmbnr"
                                    clearable
                                ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="24">
                            <el-form-item label="通知内容：" prop="tznr">
                                <el-input
                                    class="input-row"
                                    type="textarea"
                                    :rows="4"
                                    :resize="'none'"
                                    placeholder="请输入内容"
                                    v-model="addNewForm.tznr"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row v-if="addNewTitle === '新增通知模板'">
                        <el-col :span="24">
                            <el-form-item label="分类标签：" prop="flbq">
                                <div>
                                    <el-row :gutter="20">
                                        <el-col :span="16">
                                            <div>
                                                <el-input
                                                    style="width: calc(100% - 66px)"
                                                    placeholder="输入关键字进行过滤"
                                                    size="medium"
                                                    v-model="filterText2">
                                                </el-input>
                                                <el-button @click="selectTree(filterText2)" icon="el-icon-search"
                                                           size="medium"></el-button>
                                                <!--                     default-expand-all-->
                                                <el-tree
                                                    :style="treeStyleObject"
                                                    v-loading="treeLoading"
                                                    v-show="!showSelectTree"
                                                    class="filter-tree"
                                                    :filter-node-method="filterNode"
                                                    ref="tree"
                                                    :props="defaultProps"
                                                    :load="loadNode"
                                                    :highlight-current="true"
                                                    :expand-on-click-node="false"
                                                    @node-click="nodeClick"
                                                    lazy>
                                <span class="custom-tree-node" slot-scope="{ node, data }">
                                  <span>
                                        <i v-if="data.type==='tagobj'" class="el-icon-office-building"></i>
                                        <i v-else-if="data.type==='index'" class="el-icon-folder-opened"></i>
                                        <i v-else-if="data.type==='tag'" class="el-icon-price-tag"></i>
                                      {{ node.label }}
                                  </span>
                                </span>
                                                </el-tree>
                                                <el-tree
                                                    :style="{treeStyleObject}"
                                                    v-loading="treeLoading"
                                                    v-show="showSelectTree"
                                                    :data="dataTree"
                                                    default-expand-all
                                                    :props="defaultProps2"
                                                    :expand-on-click-node="false"
                                                    @node-click="nodeClick"
                                                    :highlight-current="true">
                                <span class="custom-tree-node" slot-scope="{ node, data }">
                                  <span>
                                        <i v-if="data.type==='tagobj'" class="el-icon-office-building"></i>
                                        <i v-else-if="data.type==='index'" class="el-icon-folder-opened"></i>
                                        <i v-else-if="data.type==='tag'" class="el-icon-price-tag"></i>
                                      {{ node.label }}
                                  </span>
                                </span>
                                                </el-tree>
                                            </div>
                                        </el-col>
                                        <el-col :span="7">
                                            <div class="tagSelect">
                                                <div class="title"><span style="margin-right: 20px">已选标签名称</span>  <el-button type="warning"  icon="el-icon-delete"  size="small" @click="delAllTag">全部清除</el-button></div>
                                                <span class="tag" v-for="item in tagSelectList" :key="item.tagId">{{item.tagName}}<span class="el-icon-close closeTag" @click="deleteTag(item)"></span></span>
                                            </div>
                                        </el-col>
                                    </el-row>
                                </div>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" type="primary" @click="AddSmsTemplate">确定</el-button>
                <el-button size="small" @click="addNewHandleClose">关闭</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
  import Interface1 from '@/base/mixin/power-sensitive-users';
  import Interface2 from '@/base/mixin/common-api';
  import Interface3 from '@/base/mixin/electricity-behavior';
  import bdaTable from '@/components/bda-table/bda-table'
  export default {
    name: "differenceStrategyLibrary",
    mixins: [Interface1,Interface2,Interface3],
    components:{
      bdaTable
    },
    props:{

    },
    data(){
      return {
        //导航模块变量
        selectParams:{
          dsjOptions:[],
          dsjVal:'',
          xqjOptions:[],
          xqjVal:'',
          gdsOptions: [],
          gdsVal:''
        },
        yhCodeInput:'',
        tdgdhInput:'',
        //表格
        pages:{
          pageNo:1,
          pageSize:10,
          total:11,
        },
        tableData:[],
        loading:false,
        // 通知内容配置
        notifyConfigVisible: false,
        notifyConfigForm: {
          tznrpzxz: '',
          tznrpzxzjg: "",
          time: '',
        },
        tempLoading:true,
        tempSelectName:'',
        tempList:[],
        //编辑模板
        pages2: { //新增时下拉列表分页
          pageNo: 1,
          pageSize: 10,
          total: 0,
        },
        smsId:'',//第2次停电通知内容维护id
        //通知短信模板配置
        notifyChangeVisible:false,
        mbmc:'',
        notifyChangeData: {},
        notifyChangeForm: {
          current: 1,
          size: 10,
        },
        notifyChangePageConfig: {
          name: "notifyChange",
          current: 1,
          size: 10,
          pageSizes: [10, 20, 30, 40, 50],
          layout: "prev, pager, next, sizes,jumper,total",
          total: 0
        },
        // 新增/编辑通知模板
        addNewVisible: false,
        addNewTitle: "",
        addNewForm: {
          tzmbnr: "", // 通知模板名称
          tznr: "", // 通知内容
          flbqxz: "",// 分类标签选择
        },
        //标签树 新增短信模板相关
        defaultProps: {
          children: 'children',
          label: 'text',
          isLeaf: 'leaf'
        },
        defaultProps2: {
          children: 'children',
          label: 'text',
        },
        filterText2: '',
        treeLoading: false,
        showSelectTree: false,
        dataTree: [],

        node: [],
        resolve: [],
        numb: 1,
        treeStyleObject: {
          maxHeight: 400 + 'px',
          minHeight: 300 + 'px',
          overflow: 'scroll'
        },
        tagSelectList:[],
        dxmbId:'',//短信模板id-编辑用
        //编辑模板
        // pages2: { //新增时下拉列表分页
        //   pageNo: 1,
        //   pageSize: 10,
        //   total: 0,
        // },
        // tempList:[],
        // tempSelectName:'',
        // smsId:'',//预测升档通知id
        // tempLoading:true

      }
    },
    mounted() {
      this.GetPowerAreaCode()
      this.getQueryDataList()
    },
    methods:{
      handleSizeChange(val) {
        this.pages.pageSize = val
        this.getQueryDataList()
      },
      handleCurrentChange(val) {
        this.pages.pageNo = val
        this.getQueryDataList()
      },
      tableRowClassName({row, rowIndex}) {
        if (rowIndex%2===0) {
          return 'success-row';
        }
        return '';
      },
      //获取供电单位编码--地市局
      GetPowerAreaCode() {
        this.getPowerAreaCode({
          "orgLev": "2",//2==供电单位==地市局，3==县区局局，4===供电所
          // "orgCode":"0306" //上一级编码
        }).then(res => {
          this.selectParams.dsjOptions = res
        })
      },
      //获取区县局--区县局
      GetQxjPowerAreaCode() {
        this.getPowerAreaCode({
          "orgLev": "3",//2==供电单位==地市局，3==县区局局，4===供电所
          "orgCode":this.selectParams.dsjVal //上一级编码
        }).then(res => {
          this.selectParams.xqjOptions = res
        })
      },
      //获取供电所
      GetGdsPowerAreaCode() {
        this.getPowerAreaCode({
          "orgLev": "4",//2==供电单位==地市局，3==县区局局，4===供电所
          "orgCode":this.selectParams.xqjVal //上一级编码
        }).then(res => {
          this.selectParams.gdsOptions = res
        })
      },
      //地市局选择
      dsjChange(){
        this.selectParams.gdsVal = ''
        this.selectParams.gdsOptions =[]
        this.selectParams.xqjVal =''
        this.selectParams.xqjOptions =[]
        this.GetQxjPowerAreaCode()
      },
      //区县局选择
      qxjChange(){
        this.selectParams.gdjVal = ''
        this.selectParams.gdsOptions =[]
        this.GetGdsPowerAreaCode()
      },

      //停电通知差异化策略库-列表查询
      getQueryDataList(){
        this.queryDataList({
          cityBureau:this.selectParams.dsjVal, //地市局
          countyBureau:this.selectParams.xqjVal,//县区
          powerSupply:this.selectParams.gdsVal,//供电所
          powerFailureNum:this.tdgdhInput,//工单号
          userNo:this.yhCodeInput,//用户编号
          current:this.pages.pageNo,
          size:this.pages.pageSize,
        }).then(res=>{
          this.pages.total = res.total
          this.tableData = res.records
          window.console.log(res)
        })
      },
      // 关闭通知内容配置
      notifyConfigHandleClose() {
        this.notifyConfigVisible = false
      },
      //分页
      //通知内容配置选择
      handleSizeChange2(val) {
        this.pages2.pageSize = val
        this.getTempList()
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange2(val) {
        this.pages2.pageNo = val
        this.getTempList()
        console.log(`当前页: ${val}`);
      },
      //查询
      getTempList(){
        this.tempLoading =true
        this.getSmsTemplate({
          templateName:this.tempSelectName,
          size: this.pages2.pageSize,
          current: this.pages2.pageNo,
        }).then(res=>{
          this.tempLoading =false
          this.pages2.total = res.total
          this.tempList = res.records;//编辑
        })
      },
      //点击选择
      changeTemp(item){
        window.console.log(item)
        this.notifyConfigForm.tznrpzxzjg = item.content
        this.smsId = item.id
      },
      //编辑保存
      smsConfigSet(){
        if (!this.notifyConfigForm.time){
          this.$message.warning('请选择通知时间')
          return
        }
        this.udpateMsgInfo({
          "smsId":this.smsId,
          "id":this.notifyConfigForm.tznrpzxz,
          "noticeTime":this.notifyConfigForm.time
        }).then(res=>{
          this.notifyConfigVisible = false
          this.getQueryDataList()
        })
      },
      // 操作列表
      operateList(data) {
        window.console.log(data)
        this.notifyConfigVisible = true
        this.notifyConfigForm.tznrpzxz = data.id
        this.smsId = data.smsId
        // this.tempSelectName =''
        this.getTempList()
      },

      //通知短信模板配置----
      // 关闭通知模板配置
      notifyChangeHandleClose() {
        this.notifyChangeVisible = false
      },
      // 获取短信通知模板清单
      getNotifyChangeList() {
        let columnNameData = [
          {prop: 'templateName', label: '通知模板名称'},
          {prop: 'content', label: '内容',width: 400},
          {prop: 'tagName', label: '通知模板分类标签'},
          {prop: 'creator', label: '创建人'},
          {prop: 'createTime', label: '创建时间', width: 150},
          {
            prop: "Operate",
            label: "操作",
            align: "center",
            width: "120px",
            type: [
              {
                name: "编辑",
                action: "edit"
              },
              {
                name: "删除",
                action: "del"
              }
            ]
          },
        ]
        this.getSmsTemplate({
          templateName:this.mbmc,
          size: this.notifyChangeForm.size,
          current: this.notifyChangeForm.current,
        }).then(res=>{
          this.notifyChangePageConfig.total = res.total
          this.notifyChangePageConfig.size = this.notifyChangeForm.size
          this.notifyChangePageConfig.current = this.notifyChangeForm.current
          this.tznrpzxzOptions = res.records;//编辑
          this.notifyChangeData = {
            tableData: [
              ...res.records
            ],
            headerColor: '#1899fa',
            title: "短信通知模板清单",
            name: 'notifyChange',
            columnNameData
          }
        })
      },
      //改变分页
      changePage(data) {
        if (data.name === 'maintain') {
          this.maintainQueryForm.size = data.size
          this.maintainQueryForm.current = data.current
          this.getMaintainList()
        }
        if (data.name === 'notifyChange') {
          this.notifyChangeForm.size = data.size
          this.notifyChangeForm.current = data.current
          this.getNotifyChangeList()
        }
      },
      // 点击通知模板配置
      notifyChange() {
        this.getNotifyChangeList()//通知模板
        this.notifyChangeVisible = true

      },
      // 操作通知模板列表
      operateNotifyChangeList(type, data) {
        if (type === 'edit') {
          this.addNewTitle = '编辑通知模板'
          this.addNewVisible = true
          this.addNewForm.tznr = data.content
          this.addNewForm.tzmbnr = data.templateName
          this.dxmbId = data.id
          let tagIdsArray = []
          let tagNameArray = []
          tagIdsArray = data.tagId.split(',')
          tagNameArray = data.tagName.split(',')
          for (let i=0;i<tagIdsArray.length;i++){
            this.tagSelectList.push({
              tagId:tagIdsArray[i],
              tagName:tagNameArray[i],
            })
          }
        }
        if (type === 'del') {
          this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.deleteSmsTemplate({
              id:data.id
            }).then(res=>{
              this.getNotifyChangeList()
              if (res==='删除短信模板成功'){
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
              }
            })
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
      },
      // 关闭新增通知模板
      addNewHandleClose() {
        this.tagSelectList = []
        this.addNewForm.tznr = ''
        this.addNewForm.tzmbnr = ''
        this.addNewVisible = false
      },
      // 点击新增通知模板
      addNew() {
        this.addNewVisible = true
        this.addNewTitle = "新增通知模板"
      },
      nodeClick(a) {
        if (a.type==='tag'){
          if (this.tagSelectList.length===0){
            this.tagSelectList.push({
              tagName:a.name,
              tagId:a.entryId,
            })
            return;
          }
          for (let i=0;i<this.tagSelectList.length;i++){
            if (this.tagSelectList[i].tagId===a.entryId){
              this.$message.success('当前标签以经存在')
              return;
            }
          }
          this.tagSelectList.push({
            tagName:a.name,
            tagId:a.entryId,
          })
        }
        // 这里有三个参数
      },
      //标签树
      selectTree(keywords) {
        this.treeLoading = true;
        let keywordTemp = ''
        if (keywords != '' && keywords != null) {
          keywordTemp = keywords
          this.showSelectTree = true
          this.value = ''
        } else {
          this.showSelectTree = false
          keywordTemp = this.value
          this.filterText2 = ''
        }
        this.node.childNodes = [];
        this.getBdaTagList({
          keyword: keywordTemp,
          id: 0,
        }).then(res => {
          this.dataTree = res
          this.treeLoading = false;
          this.resolve(this.dataTree)
        }).catch(() => {

        });
        //手动调用加载数据方法
        // this.loadNode(this.node, this.resolve);
      },
      filterNode(value, data) {
        if (!value) return true;
        return data.text.indexOf(value) !== -1;
      },
      loadNode(node, resolve) {
        //加载数据的方法里把node，resolve存起来 ==== 只存初始化时的数据
        if (this.numb === 1) {
          this.node = node;
          this.resolve = resolve;
          // 把子节点清空，否则下次加载时会直接往里push子节点导致重复
          this.node.childNodes = [];
          this.numb++
        }
        // let permissionArr =[]
        // permissionArr = ['1','4']

        this.dataTree = []
        let $this = this
        if (node.data) {
          if (node.data.type) {
            this.getBdaTagList(
              {
                id: node.data.entryId,
                type: node.data.type,
                //filterStatus:[3,5,7], //[1,2,4,6],//过滤除了“入库3”、“停用5”、“下线7”的标签；
                // filterTagRuleTypes: [3, 4]//过滤其他标签，只要大数据平台  1==大数据平台，3==手工标记 ，4==人工智能平台
              }).then(res => {
              $this.dataTree = res
              $this.dataTree.forEach(item => {
                if (item.type == 'tag') {
                  item.leaf = true
                }
              })
              this.treeLoading =false;
              resolve($this.dataTree)
            }).catch(() => {

            });
          } else {
            resolve([])
          }

        } else {
          let params = {
            // keyword:'11',
            //filterStatus: ["7"],//过滤下线标签
            // filterTagRuleTypes: qs.stringify( permissionArr,{ arrayFormat: "repeat" })//过滤其他标签，只要手工标签
          };
          this.treeLoading =true;
          this.getBdaTagList(params).then(res => {
            $this.options = res
            for (let i = 0; i < res.length; i++) {
              res[i].disabled = true;
            }
            this.dataTree = res
            this.treeLoading =false;
            resolve(this.dataTree)
          }).catch(() => {

          });

        }
      },
      //新增短信模板 // 或者编辑通知模板
      AddSmsTemplate(){
        let tagIdArray = []
        let tagName = []
        for (let i=0;i<this.tagSelectList.length;i++){
          tagIdArray.push(this.tagSelectList[i].tagId)
          tagName.push(this.tagSelectList[i].tagName)
        }
        if (  this.addNewTitle === '编辑通知模板'){
          this.updateSmsTemplate({
            "content":this.addNewForm.tznr,
            "templateName":this.addNewForm.tzmbnr,
            "id":this.dxmbId
          }).then(()=>{
            this.addNewHandleClose()
            this.getNotifyChangeList()
          })
        }else if (this.addNewTitle === '新增通知模板'){
          this.addSmsTemplate({
            "content":this.addNewForm.tznr,
            "templateName":this.addNewForm.tzmbnr,
            "id":tagIdArray.join(),
            "tagName":tagName.join(),
          }).then(()=>{
            this.addNewHandleClose()
            this.getNotifyChangeList()
          })
        }
      },
      //删除已选单个标签
      deleteTag(item){
        this.tagSelectList = this.tagSelectList.filter(items=>{
          if (items.tagId !== item.tagId){
            return items
          }
        })
      },
      //删除已选标签
      delAllTag(){
        this.tagSelectList = []
      }
    }
  }
</script>

